<template>
	<view>
		<view class="minePage">
			<view class="half_circle">
				<view class="minePage0">
					<image class="minePage1"
						:src="userInfo.avatarUrl?userInfo.avatarUrl:'../../static/images/photo.png'" mode=""></image>
					<view class="minePage2">
						<button v-if="!userInfo" type="default" class="okno4" @click="clickBtn">
							点击授权
						</button>
						<text v-else>{{userInfo.nickName}}</text>
					</view>
				</view>
			</view>


		</view>
		<!-- 	<view class="minePageJlu">
			 <view class="minePageF">充值记录 </view>
			 <view class="minePageF"></view>
			 <view class="minePageF"></view>
		</view> -->
		<view class="margintopBottom">
			<!-- https://ext.dcloud.net.cn/plugin?id=24 -->
			<uni-list>
				<uni-list-item link to="/pages/chongzhijilu/index"   title="充值记录" :thumb="czjl" thumb-size="medium"></uni-list-item>

			</uni-list> 
			<!-- <uni-list @click="copys('wo9999vip')" >
				<uni-list-item      title="请联系客服" :thumb="xieyi" thumb-size="medium"></uni-list-item>
            </uni-list> -->
		</view>

	</view>
</template>

<script>
	import {
		login,
		loginInfo
	} from "@/api/index.js"
	export default {
		name: 'index',

		data() {
			return {
				userInfo: {},
				xieyi: require('../../static/images/xieyi.png'),
				czjl: require('../../static/images/czjl.png'),

			}
		},
		onLoad() {

		},
		onShow() {
			this.userInfo = uni.getStorageSync('userlist')
		},
		methods: {
			/*
			            复制按钮的实现
			            */
			            copys(value) {
			                //提示模板
			                uni.showModal({
			                    content: value, //模板中提示的内容
			                    confirmText: '复制微信',
			                    success: () => { //点击复制内容的后调函数
			                        //uni.setClipboardData方法就是讲内容复制到粘贴板
			                        // API `setClipboardData` is not yet implemented
			                        //意思是H5端没有这个接口！！！
			                        uni.setClipboardData({
			                            data: value, //要被复制的内容
			                            success: function() {
			                                    //重点~做笔记
			                                    //在success中加入uni.hideToast()可以解决
			                                    uni.hideToast({
			                                        title: '复制成功',
			                                        duration: 2000,
			                                        icon: 'none'
			                                    });
			                                    //以下就可自定义操作了~
			                                },
			                                fail: function(err) {
			                                    uni.showToast({
			                                        title: '复制失败',
			                                        duration: 2000,
			                                        icon: 'none'
			                                    });
			                                }
			                        });
			                    }
			                });
			            },
			clickBtn() {
				var _this = this
				uni.getUserProfile({
					desc: '获取你的昵称、头像、地区及性别',
					success: res => {
						var avatarUrl = res.userInfo.avatarUrl
						var username = res.userInfo.nickName
						uni.setStorageSync('userlist', res.userInfo);

						uni.switchTab({
							url: '/pages/index/index'
						})
						uni.showToast({
							title: "授权成功",
							icon: 'none'
						})
						let user = {
							uid: uni.getStorageSync('uid'),
							nickname: username,
							headUrl: avatarUrl
						}
						_this.updateUserInfo(user)
					},
					fail: res => {

						//拒绝授权
						wx.showToast({
							title: '您拒绝了请求,不能正常使用小程序',
							icon: 'error',
							duration: 2000
						});
						return;
					}
				});


			},
			 
			// 更新用户信息
			updateUserInfo(user) {

				loginInfo({
					...user
				}).then(response => {});

			},
			uniListItem(e) {
				let Url = ""
				if (e == "sy") {
					Url = '/pages/chongzhijilu/index'
				} else {
					Url = '/pages/xieyi/index'
				}

				uni.navigateTo({
					url: Url
				})
			}
		}
	}
</script>

<style scoped>
	.minePage {
		width: 100%;
		display: flex;

		flex-direction: row;
		align-items: center;
	}

	.minePageF {
		font-size: 30rpx;
		color: #fff;
	}

	.minePageJlu {
		margin: 0 30rpx;
		height: 162rpx;
		background-image: linear-gradient(to right, #f6aa52 0%, #f6aa52 100%);
		border-radius: 10rpx;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-around;
	}

	.okno4 {

		/* background-image: linear-gradient(to right, #66ccff 0%, #66cccc 100%); */
		color: #fff !important;
		background-color: inherit;

	}

	.okno4::after {
		border: none;
	}

	.minePage0 {
		width: 100%;
		padding: 20upx;
		display: flex;
		flex-direction: row;
		align-items: center;

	}

	.minePage1 {
		width: 100rpx;
		height: 100rpx;
		border-radius: 50rpx;

	}

	.minePage2 {
		font-size: 43upx;
		color: #fff;
		margin-left: 20upx;
	}

	.half_circle {
		position: relative;
		width: 100%;
		height: 100px;
		color: #fff;
		display: flex;
		background-image: linear-gradient(to right, #66ccff 0%, #66cccc 100%);
	}

	.half_circle:after {
		position: absolute;
		left: 0;
		right: 0;
		bottom: -20px;
		z-index: -1;
		content: '';
		height: 40px;
		width: 100%;
		border-radius: 0 0 50% 50%;
		background-image: linear-gradient(to right, #66ccff 0%, #66cccc 100%);
	}

	.margintopBottom {
		margin-top: 64upx;
	}
</style>
